.playSquare {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  max-width: 50px;
  min-width: 50px;
  height: 50px;
  margin: 0 5px;
  border: 3px #fff solid;
  cursor: pointer;
  padding: 10px;
  border-radius: 3px
}

.playSquare.inactive {
  cursor: disabled;
  opacity: .65
}

.fullWidthPlaySquare {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-width: 50px;
  height: 50px;
  margin: 0 5px;
  border: 3px #fff solid;
  cursor: pointer;
  padding: 10px;
  border-radius: 3px;
  width: 100%
}

.fullWidthPlaySquare.inactive {
  opacity: .65;
  cursor: not-allowed
}


